document.getElementById("btn25-1").onclick = (event) => {
    event.preventDefault();
    //1.–ù–∞–ø–∏—Å–∞—Ç—å —Ñ—É–Ω–∫—Ü–∏—é, –∫–æ—Ç–æ—Ä–∞—è –ø—Ä–∏–Ω–∏–º–∞–µ—Ç 2 —á–∏—Å–ª–∞ –∏ –≤–æ–∑–≤—Ä–∞—â–∞–µ—Ç -1, –µ—Å–ª–∏ –ø–µ—Ä–≤–æ–µ –º–µ–Ω—å—à–µ, —á–µ–º –≤—Ç–æ—Ä–æ–µ;
    // 1 ‚Äì –µ—Å–ª–∏ –ø–µ—Ä–≤–æ–µ –±–æ–ª—å—à–µ, —á–µ–º –≤—Ç–æ—Ä–æ–µ; –∏ 0 ‚Äì –µ—Å–ª–∏ —á–∏—Å–ª–∞ —Ä–∞–≤–Ω—ã.
    alert(`if a > b = 1; a < b = -1; a === b = 0`);

    function checkNumbers(a, b) {
        if (a < b) {
            return "-1";
        } else if (a > b) {
            return "1";
        }
        return "0";
    }
    const a = +prompt(`insert a`, 50);
    const b = +prompt(`insert b`, 50);
    alert(checkNumbers(a, b));
};
