document.getElementById("btn25-5").onclick = (event) => {
    event.preventDefault();
    //5.–ù–∞–ø–∏—Å–∞—Ç—å —Ñ—É–Ω–∫—Ü–∏—é, –∫–æ—Ç–æ—Ä–∞—è –ø—Ä–æ–≤–µ—Ä—è–µ—Ç, —è–≤–ª—è–µ—Ç—Å—è –ª–∏ –ø–µ—Ä–µ–¥–∞–Ω–Ω–æ–µ –µ–π —á–∏—Å–ª–æ —Å–æ–≤–µ—Ä—à–µ–Ω–Ω—ã–º.
    //–°–æ–≤–µ—Ä—à–µ–Ω–Ω–æ–µ —á–∏—Å–ª–æ ‚Äì —ç—Ç–æ —á–∏—Å–ª–æ, —Ä–∞–≤–Ω–æ–µ —Å—É–º–º–µ –≤—Å–µ—Ö —Å–≤–æ–∏—Ö —Å–æ–±—Å—Ç–≤–µ–Ω–Ω—ã—Ö –¥–µ–ª–∏—Ç–µ–ª–µ–π.
    const checkIsPerfect = +prompt(`Enter number for check is perfect`, 28);

    function isPerfect(a) {
        if (a === 0) {
            return false;
        }
        let calc = 0;
        for (let i = 1; i < a; i++) {
            if (a % i === 0) {
                calc += i;
            }
        }
        console.log(calc);
        return calc === a;
    }
    alert(`Is perfect ${isPerfect(checkIsPerfect)}`);
};
