document.getElementById("btn25-6").onclick = (event) => {
    event.preventDefault();
    //6.–ù–∞–ø–∏—Å–∞—Ç—å —Ñ—É–Ω–∫—Ü–∏—é, –∫–æ—Ç–æ—Ä–∞—è –ø—Ä–∏–Ω–∏–º–∞–µ—Ç –º–∏–Ω–∏–º–∞–ª—å–Ω–æ–µ –∏ –º–∞–∫—Å–∏–º–∞–ª—å–Ω–æ–µ –∑–Ω–∞—á–µ–Ω–∏—è –¥–ª—è –¥–∏–∞–ø–∞–∑–æ–Ω–∞,
    //–∏ –≤—ã–≤–æ–¥–∏—Ç —Ç–æ–ª—å–∫–æ —Ç–µ —á–∏—Å–ª–∞ –∏–∑ –¥–∏–∞–ø–∞–∑–æ–Ω–∞, –∫–æ—Ç–æ—Ä—ã–µ —è–≤–ª—è—é—Ç—Å—è —Å–æ–≤–µ—Ä—à–µ–Ω–Ω—ã–º–∏.
    //–ò—Å–ø–æ–ª—å–∑—É–π—Ç–µ –Ω–∞–ø–∏—Å–∞–Ω–Ω—É—é —Ä–∞–Ω–µ–µ —Ñ—É–Ω–∫—Ü–∏—é, —á—Ç–æ–±—ã —É–∑–Ω–∞–≤–∞—Ç—å, —Å–æ–≤–µ—Ä—à–µ–Ω–Ω–æ–µ —á–∏—Å–ª–æ –∏–ª–∏ –Ω–µ—Ç.
    const findFrom = +prompt("Find perfect number from diapason, enter from", 10);
    const findTo = +prompt("Find perfect number from diapason, enter to", 10000);

    function isPerfect(a) {
        if (a === 0) {
            return false;
        }
        let calc = 0;
        for (let i = 1; i < a; i++) {
            if (a % i === 0) {
                calc += i;
            }
        }
        return calc === a;
    }

    function findPerfect(mn, mx) {
        const perfects = [];
        for (let i = mn; i < mx; i++) {
            if (isPerfect(i)) {
                perfects.push(i);
            }
        }
        return perfects;
    }

    alert(`perfect numbers is ${findPerfect(findFrom, findTo)}`);
};
