document.getElementById("btn25-9").onclick = (event) => {
    event.preventDefault();
    //9.–ù–∞–ø–∏—Å–∞—Ç—å —Ñ—É–Ω–∫—Ü–∏—é, –∫–æ—Ç–æ—Ä–∞—è –ø—Ä–∏–Ω–∏–º–∞–µ—Ç –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ —Å–µ–∫—É–Ω–¥,
    // –ø–µ—Ä–µ–≤–æ–¥–∏—Ç –∏—Ö –≤ —á–∞—Å—ã, –º–∏–Ω—É—Ç—ã –∏ —Å–µ–∫—É–Ω–¥—ã –∏ –≤–æ–∑–≤—Ä–∞—â–∞–µ—Ç –≤ –≤–∏–¥–µ —Å—Ç—Ä–æ–∫–∏ ¬´—á—á:–º–º:—Å—Å¬ª
    const inputSeconds = +prompt(
        "Enert please, how many seconds convert to <<hh.mm.ss>>",
        50000
    );

    const convertSeconds = (s) => {
        const h = Math.floor(s / (60 * 60));
        s %= 60 * 60;
        const m = Math.floor(s / 60);
        const sec = s % 60;
        return "<<" + h + ":" + m + ":" + sec + ">>";
    };
    alert(convertSeconds(inputSeconds));
};
